
*{
  font-family: Arial, Helvetica, sans-serif;
}

body{
  padding: 100px;
}

input{
color: green;
  font-size: 1.3rem;
width: 100%;

}

.card {
align-content: center;
border: 1px solid green;
border-radius: 6px;
overflow: hidden;
margin-bottom: 10px;
margin-right: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;
}

.cardgrid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}